@import '../../../css/module/color';
@import '../../../css/module/fn';

$slider-height: 230;

body {
	overflow: hidden;
}

#slider {
	height: p2m(700);
	background-color: #fff;

	img {
		//width: 10rem;
		height: p2m($slider-height);
		display: block;
		margin: 0 auto;
		padding: 20px;
		//background-color: #777777;
	}
	.ct {
		@include textClip;
		font-size: 14px;
		padding: 0 1em;
		position: relative;
		height: 1.3rem;
		line-height: 1.3rem;
		margin-top: -1.3rem;
		color: #fff;
		background-color: rgba(0, 0, 0, 0.6);
	}
}
#banner {
	position: absolute;
	z-index: 2;
	width: 100%;
}

.mui-inner-wrap {
	background-color: #fff;
}

#message {
	height: p2m(96);
	line-height: p2m(96);
	background-color: #eee;
	padding: 0 0.5rem 0 1.6rem;
	font-size: 0.3rem;
	text: {
		//indent: 1.5rem;
		overflow: ellipsis;
	}
	overflow: hidden;
	background: {
		image: url('/img/ico96-lb.png');
		size: 1.28rem;
		repeat: no-repeat;
		position: left center;
	}
	white-space: nowrap;
}

#myScroll {
	position: absolute;
	top: p2m($slider-height);
	bottom: 0;
	overflow: scroll;
	background-color: $background-red;
}

#board {
	width: 100%;
	table-layout: fixed;
	td {
		height: 3.1rem;
		border: 1px solid #ddd;
		border-collapse: collapse;
		background-color: #474747;
		text: {
			align: center;
			transform: capitalize;
		}
		font: {
			size: 0.35rem;
		}

		&:nth-of-type(3n + 0) {
			border-right: none;
		}
		&:nth-of-type(3n-2) {
			border-left: none;
		}

		> a {
			color: #fff;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;

			&:active,&:hover{
				background-color: #888;
			}
		}
	}

	.icoDashboard {
		display: block;
		width: 1.28rem;
		height: 1.28rem;
		margin: 0 auto 0.2rem;
	}
}

#icoMenu {
	position: absolute;
	top: 0.6rem;
	left: 0.6rem;
	color: #555;
}
.mui-icon-bars:before,
.mui-icon-extra-sweep:before {
	font-size: 32px;
	display: block;
	color: #555;
	width: 0.8rem;
	height: 0.8rem;
	//margin: 0.6rem 0 0 0.4rem;
}
.mui-icon .mui-badge {
	margin-left: -0.3em !important;
}

#imgQR {
	width: 0.8rem;
	height: 0.8rem;
	margin: 0.6rem 0.8rem 0 0;
}

#imgLOGO {
	width: p2m(100);
	height: p2m(100);
	position: absolute;
	top: p2m(25);
	right: p2m(25);
}

#offCanvasSide {
	background: {
		image: url('./bg-leftMenu2.jpg');
		size: 100% 100%;
		repeat: no-repeat;
	}
}

.onlyForExhibition {
	display: none;
}

#imgAvatar {
	width: 1.4rem;
	height: 1.4rem;
	display: block;
	margin: 0.3rem auto 0.2rem;
}
#spUserName {
	text-align: center;
	font-size: 0.45rem;
	color: #fff;
}
#spRealName {
	text-align: center;
	font-size: 0.35rem;
	color: #bbb;
	margin-bottom: 0.5rem;
}

#divITB {
	display: block;
	//position: absolute;
	//bottom: 0.4rem;
	margin-top: 9rem;
	left: 0;
	text-align: center;
	width: 100%;

	img {
		width: 2.2rem;
		margin: 0 auto 0.2rem;
	}

	span {
		display: block;
		font-size: 0.3rem;
	}
}

#lmMyScore2,
#lmMyScore {
	display: none;
}

#offCanvasSideScroll {
	top: 6.66rem;
	.mui-table-view {
		background-color: #deb5a7;
		li {
			color: #89351e;

			img {
				width: 16px;
				height: 16px;
				vertical-align: sub;
			}
		}
	}
	.mui-table-view:after,
	.mui-table-view-cell:after {
		background-color: #89351e;
	}
}

#topPopover {
	position: fixed;
	top: 16px;
	right: 10px;
	height: 100px;
	width: 160px;

	.mui-popover-arrow {
		left: auto;
		right: 6px;
	}
}
